@font-face {
  font-family: "Poppins";
  font-weight: 400;
  src: url("./../fonts/poppins-regular.woff2") format("woff2"), url("./../fonts/poppins-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-weight: 500;
  src: url("./../fonts/poppins-medium.woff2") format("woff2"), url("./../fonts/poppins-medium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-weight: 700;
  src: url("./../fonts/poppins-bold.woff2") format("woff2"), url("./../fonts/poppins-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 400;
  src: url("./../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("./../fonts/cormorantgaramond-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 600;
  src: url("./../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("./../fonts/cormorantgaramond-semibold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 700;
  src: url("./../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("./../fonts/cormorantgaramond-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-style: italic;
  src: url("./../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("./../fonts/cormorantgaramond-italic.woff") format("woff");
  font-display: swap; }

body,
html {
  height: 100%; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "Poppins", "Courier New", "Courier", monospace;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  position: relative; }

.container {
  max-width: 1366px;
  margin: 0 auto; }

.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100%; }

.content {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto; }

.footer {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

main {
  position: relative; }
